<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :model="model"
        :use-default-action="false"
        :use-customer-action-only="true"
        :use-select="false"
        :show-expand="true"
        :single-expand="true"
        icon="mdi-clipboard-list"
        :entity-name="$t('打印单列表')"
      >
        <template v-slot:expanded-item="{item }">
          <div
            class="text-center"
            v-html="item.content"
          />
        </template>
        <template v-slot:footer>
          <v-toolbar
            color="white"
            flat
          />
        </template>
        <template v-slot:item.action="{item}">
          <v-icon
            class="mr-2"
            small
            @click="reprint(item)"
          >
            mdi-printer
          </v-icon>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import PrinterList from '../../model/PrinterList'
  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: PrinterList,
      })
    },
    methods: {
      async reprint (item) {
        await this.model.reprint(item)
        IKUtils.toast()
      },
    },
  }
</script>
